.loader{
    margin: 25px;
    border:rgb(109, 11, 201) 50px groove;
    border-radius: 100px;
    width: 50px;
    height: 50px;
    animation: loader ease-out 1s infinite;
    }
    @keyframes loader{
        from{
        transform:rotate(0deg)
        }
        to{
            transform:rotate(360deg)
        }
    }
